<template>
	<view class="home">
		<navbar :isSearch="true" @input="change" v-model="value"></navbar>
		<view class="home-list">
			<view class="label-box" v-if="is_histroy">
				<view class="label-header">
					<text class="label-title">搜索历史</text>
					<text class="label-clear" @click="clear">清空</text>
				</view>
				<view class="label-content" v-if="historyLists.length > 0">
					<view class="label-content__item" v-for="(item,index) in historyLists" @click="openHistroy(item)" :key="index">
						{{item.name}}
					</view>
				</view>
				<view class="no-data" v-else>
					没有搜索历史
				</view>
			</view>
			<!-- <button type="default" @click="test">测试</button> -->
			<list-scroll class="list-scroll"  v-else>
				<uni-load-more v-if="loading" status="loading" iconType="snow"></uni-load-more>
				<view v-if="searchList.length > 0">
					<list-card  :item="item" v-for="(item,index) in searchList" :key="index" @click="setHistory"></list-card>
				</view>
				<view class="no-data" v-if="searchList.length === 0 && !loading">
					没有搜索到相关数据
				</view>
			</list-scroll>
		</view>
	</view>
</template>

<script>
	import { mapState } from 'vuex'
	export default {
		data() {
			return {
				value:'',
				is_histroy:true,
				searchList:[],
				loading:false
			}
		},
		computed:{
			...mapState(['historyLists'])
		},
		methods: {
			setHistory(){
				// 点击卡片，返回
				this.$store.dispatch('set_history',{
					name:this.value
				})
			},
			openHistroy(item){
				this.value = item.name
				this.getSearch(this.value)
			},
			change(value){
				if(!value) {
					clearTimeout(this.timer)
					this.mark = false
					this.getSearch(value)
					return
				}
				// this.is_histroy = false
				// this.getList(value)
				console.log(value,'接受的value')
				if(!this.mark){
					this.mark = true
					this.timer = setTimeout(()=>{
						this.mark = false
						this.getSearch(value)
					},1000)
				}
			},
			clear(){
				this.$store.dispatch('clearHistory')
				uni.showToast({
					title:'清空完成'
				})
			},
			getSearch(value){
				if(!value){
					this.searchList = []
					this.is_histroy = true
					return
				}
				this.is_histroy = false
				this.loading = true
				this.$api.get_search({
					value,
				}).then(res => {
					this.searchList = res.data
					console.log(res.data)
					this.loading = false
				}).catch(() => {
					this.loading = false
				})
			},
			
		}
	}
</script>

<style lang="scss">
 page{
	 height: 100%;display: flex;background-color: #f5f5f5;
}
	 .home{ 
		 display: flex;flex-direction: column;flex: 1;
		 .label-box{
			 background-color: #fff;margin-bottom: 10px;
			 .label-header{
				 display: flex;justify-content: space-between;
				 font-size: 14px;color: #666;padding: 10px 15px;border-bottom: 1px #f5f5f5 solid;
				 .label-title{ color: $mk-base-color; }
				 .label-clear{ color: #30b33a;font-weight: bold; }
			 }
			 .label-content{
				display: flex;flex-wrap: wrap;padding: 15px;padding-top: 0px;
				 .label-content__item{ padding: 2px 10px;margin-top: 12px;margin-right: 10px;border-radius: 5px;border: 1px #666 solid;font-size: 14px;color: #666; }
			 }
		 }
	}
 
 .no-data{
	 height: 200px;line-height: 200px;width: 100%;text-align: center;color: #666;font-size: 12px;
	}
</style>
